'use client';

import { Typography, ButtonBase } from '@mui/material';
import Link from 'next/link';
import { usePathname } from 'next/navigation';
import StyledBottomNavigation, { classes } from './styles';
import { bottomNavigationTabs as tabs, bottomNavigationCheckRouteActive as checkRouteActive } from './data';

const BottomNavigation: React.FC = () => {
  const pathname = usePathname() ?? '';

  return (
    <StyledBottomNavigation>
      {tabs.map((item) => {
        const isActive = checkRouteActive(pathname, item);
        return (
          <ButtonBase
            component={Link}
            key={item.key}
            href={item.route}
            aria-current={isActive ? 'page' : undefined}
            replace
            className='flex flex-col h-16 gap-2 min-w-24'>
            <div className={isActive ? classes.iconActive : classes.iconInactive}>{item.icon}</div>
            <Typography className={isActive ? classes.textActive : classes.textInactive}>{item.iconName}</Typography>
          </ButtonBase>
        );
      })}
    </StyledBottomNavigation>
  );
};

export default BottomNavigation;
